import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Container } from '@components/global';
import ExternalLink from '@common/ExternalLink';

const SurveyHeader = () => (
  <StaticQuery
    query={graphql`
      query {
        art_survey: file(
          sourceInstanceName: { eq: "art" }
          name: { eq: "survey" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    `}
    render={data => (
      <HeaderWrapper>
        <Container>
          <Grid>
            <Art>
              <Img fluid={data.art_survey.childImageSharp.fluid} />
            </Art> 
            <Text>
              <h1>
                <b>Your opinions can break the stigma of hearing loss!</b>
              </h1>
              <br/>
              <p><small>
              Hi, we are Yentsen and Kevin. 
              We are developing a universal earphone product for those who have normal hearing or have some degree of hearing loss.
              We would love to gather feedback on this design concept for further improvements. 
              The survey will take about 10 minutes. All of your feedback is highly appreciated! 
              </small></p>
              <br />
              <button><StyledExternalLink href="https://docs.google.com/forms/d/e/1FAIpQLSdmNQ4zKgkenpGMGSuTLQN0_i3Ztvv4k-c5DgNfvz0BLZJtpw/viewform">
                  Take the survey &nbsp;&#x2794;
              </StyledExternalLink></button>
              <br /><br />
              <p><small>
              If you have any questions, please don’t hesitate to drop us a message at hello[at]rearia.io  
              <br /><br />
              © Copyright Rearia Co., Ltd. 2019.</small></p>
            </Text>
          </Grid>
        </Container>
      </HeaderWrapper>
    )}
  />
);

const HeaderWrapper = styled.header`
  background-color: ${props => props.theme.color.primary};
  padding-top: 96px;

  @media (max-width: ${props => props.theme.screen.md}) {
    padding-top: 128px;
  }
`;

const Art = styled.figure`
  width: 120%;
  margin-left: -100px;
  margin-bottom: 20vh;

  > div {
    width: 100%;
    margin-bottom: -4.5%;

    @media (max-width: ${props => props.theme.screen.md}) {
      width: 100%;
      margin-left: 0px;
    }
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  grid-gap: 64px;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    grid-gap: 80px;

    > ${Art} {
      order: 2;
    }
  }
`;

const Text = styled.div`
  justify-self: start;

  @media (max-width: ${props => props.theme.screen.md}) {
    justify-self: start;
  }
`;

const StyledExternalLink = styled(ExternalLink)`
  color: inherit;
  text-decoration: none;
  padding: 10px 20px 10px 20px;
  line-height: 40px;
  background-color: ${props => props.theme.color.white.dark};
  border-radius: 3px; 

  &:hover {
    color: ${props => props.theme.color.black.regular};
    background-color: ${props => props.theme.color.white.regular};
    transition: 0.5s;
  }
`;

export default SurveyHeader;
